import React from 'react';
import './AboutPlatform.scss';

const AboutPlatform = ({tasks}) => {
    const tasksImages = tasks.map((taskItem, i) => <img src={taskItem} alt="tasks-icon" className="about-platform-icon-item" key={i}/>);
    

    return (
      <section className="about-platform">
    <div className="container">
            <div className="about-platform-block">
            <h2 className="about-platform-title">Про платформу</h2>
            <p className="about-platform-title-content"> Проект із захисту лелек – продовження глобальної політики ДТЕК із сталого розвитку і відповідає напрямку Глобального договору ООН – «збереження біорізноманіття видів живих істот». Компанія розпочала його реалізацію у 2012 році у Дніпропетровські та Донецькій областях, перше спеціальне лелече «безпечне» гніздо встановили восени 2013 року в с. Піщанка Новомосковського району на Дніпропетровщині. Наразі програма поширюється на інші області України.
            <br />Мережі - одна з найбільших інфраструктур в Україні: тільки в ДТЕК - більше 186 000 км мереж. Моніторінг мереж проводиться на регулярній основі.
                <br />Дана платформа розроблена для своєчасного виявлення лелечих гнізд на лініях електропередач та вживання заходів для вирішення наступних питань:
                </p>
                </div>
            <div className="about-platform-icon">
              {tasksImages}
              </div>
              <div className="about-platform-icon-text">
                              <div className="about-platform-back">
              <h1 className="about-platform-space">Енергетичні:</h1>
               <ul className="about-platform-space-item">
                 <li>загибель птахів у пожежі чи від виского струма;<br/><br/></li>
               <li>локальні пожежі.</li>
               </ul>
              </div>
              <div className="about-platform-back">
              <h1 className="about-platform-space">Технічні:</h1>
               <ul className="about-platform-space-item">
                 <li>обриви ліній;<br/><br/></li>
               <li>відключення від струму населенних пунктів.</li>
               </ul>
              </div>
              <div className="about-platform-back">
              <h1 className="about-platform-space">Бізнесові:</h1>
               <ul className="about-platform-space-item">
                 <li>страждає репутація компанії, як надійного постачальника енергії;<br/><br/></li>
               <li>непередбачувані витрати коштів та часу на ремонт.</li>
               </ul>
              </div>
              </div>
              
            </div>
    </section>
    );
};
export default AboutPlatform;