import React, {useEffect, useRef, useState} from 'react';
import mapboxgl from 'mapbox-gl';
import './AirQuality.scss';
import 'mapbox-gl/dist/mapbox-gl.css';

mapboxgl.accessToken = 'pk.eyJ1IjoieWFtbnlrIiwiYSI6ImNrZmxhZjZscjAwb2gycm54aWo2bGJvZnEifQ.BbYutmem-GZtYciYeMln1A';

const AirQuality = () => {
    const mapContainer = useRef(null);
    const [locationInfo] = useState({
        lng: 31,
        lat: 48.3,
        zoom: 5
    });

    useEffect(() => {
        const map = new mapboxgl.Map({
            container: mapContainer.current,
            style: 'mapbox://styles/mapbox/outdoors-v11',
            center: [locationInfo.lng, locationInfo.lat],
            zoom: locationInfo.zoom
        });
    }, [])

    return (
      <section className={'air-q container'}>
<h2 className="air-q-title">Мапа розташування пташиних гнізд на лінії електропередачі</h2>
<div className="air-q-text">
    <div className="air-q-point"><div className="air-q-text-new"></div><div className="p"><p>потребуєть перевірки</p></div></div>
    <div className="air-q-point"><div className="air-q-text-work"></div><div className="p"><p>в роботі</p></div></div>
    <div className="air-q-point"><div className="air-q-text-done"></div><div className="p"><p>вжито заходів</p></div></div>
</div>
<div className="air-q-map" ref={mapContainer}/>
<div>
<a href="#" className="air-q-link-button-new">Список нових об'єктів</a>
<a href="#" className="air-q-link-button-work">Список об'єктів в роботі</a>
<a href="#" className="air-q-link-button-done">Список виконаних об'єктів</a>
</div>
      </section>
    );
    };

    export default AirQuality;