import React from 'react';
import './Footer.scss';

const Footer = ({partners, author, designer}) => {
const partnersLogos = partners.map((logo, i) => <img key={i} src={logo} alt={'partner logo'} className="footer-partners-item"/>);

return (
    <footer className="footer">
        <div className="container">
            <div className="footer-partners">
                {partnersLogos}
            </div>

            <div className="footer-text-wrap">
            <div className="footer-nav">
            <a href="" className="footer-nav-link">Про платформу</a>
            <a href="#" className="footer-nav-link">Вхід/Реєстрація</a>
            <a href="#" className="footer-nav-link">Завантажити об'єкт</a>            </div>
            <p className="footer-nav-text">
Платформа розроблена <span className="text-yellow">{author}</span>, в рамках учбової програми IT Nation Україна.
            </p>
        </div>
<p className="footer-text">
            2020, Всі права захищені
            </p>
        <p className="footer-text">
            Політика конфеденційності
        </p>   
        </div>
    </footer>
);
};

export default Footer;