import React from 'react';
import './HeroBlock.scss';

const HeroBlock = ({title, logos, onChange}) => {
    const logosImages = logos.map((logoItem, i) => <img src={logoItem} alt="partner-logo" className="hero-partners-item" key={i}/>);
      
   
    return (
        <section className="hero">
        <div className="container">
            <div className="hero-block">
            <div className="hero-partners">
            {logosImages}
           </div>
           <div className="hero-seach">
             <div>
               <input className="hero-seach-panel" placeholder="Пошук" type="text" />
               <button className="button" type="button">Пошук</button></div>
                </div>
            </div>
           
           <h1 className="hero-title">{title}</h1>
           
<a href="#" className="hero-link-platform">Про платформу</a>
<a href="AboutPlatform" className="hero-link-enter">Вхід</a>
<a href="#" className="hero-link-registration">Реєстрація</a>
<a href="#" className="hero-link-load">Завантажити об'єкт</a>
          </div>
          
        </section>
            );
};
export default HeroBlock;